
import { DataNotify } from '../../base/DataNotify';


export class HallData extends DataNotify {
    public static __ins:HallData = null;

    public static GetInstance() : HallData
    {
        if(HallData.__ins == null)
        {
            HallData.__ins = new HallData();
            HallData.__ins.CreateNotify();
        }

        return HallData.__ins;
    }
    

}

